<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>店铺认证</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <h4>申请资料填写</h4>
      <el-form ref="form" :model="form" :rules="rules" label-position="right" label-width="130px">
        <el-form-item label="商铺名称：" prop="shopName">
          <el-input v-model="form.shopName"></el-input>
        </el-form-item>
        <el-form-item label="联系人：" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="联系人电话：" prop="mobile">
          <el-input v-model="form.mobile"></el-input>
        </el-form-item>
        <el-form-item label="地区：" prop="area">
          <areaSelectCmp :area="form.area" @childFn="onChild"></areaSelectCmp>
        </el-form-item>
        <el-form-item label="详细地址：" prop="adderss">
          <el-input v-model="form.adderss"></el-input>
        </el-form-item>
        <el-form-item label="税号：" prop="tax">
          <el-input v-model="form.tax"></el-input>
        </el-form-item>
        <el-form-item label="身份证：" required>
          <div class="identityCardUrl">
            <el-upload class="avatar-uploader" action="http-request" :http-request="onIdentityCardUrlPositive" :show-file-list="false">
              <el-image v-if="identityCardUrlPositive" :src="identityCardUrlPositive" fit="contain" class="avatar"></el-image>
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
            <el-upload class="avatar-uploader" action="http-request" :http-request="onIdentityCardUrlNegative" :show-file-list="false">
              <el-image v-if="identityCardUrlNegative" :src="identityCardUrlNegative" fit="contain" class="avatar"></el-image>
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </div>
        </el-form-item>
        <el-form-item label="营业执照：" required>
          <el-upload class="avatar-uploader" action="http-request" :http-request="onBusinessLicenseUrl" :show-file-list="false">
            <el-image v-if="form.orgUrl" :src="form.orgUrl" fit="contain" class="avatar"></el-image>
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="门头照：" required>
          <div class="identityCardUrl">
            <el-upload class="avatar-uploader" action="http-request" :http-request="onDoorHeadUrl0" :show-file-list="false">
              <el-image v-if="doorHeadUrl0" :src="doorHeadUrl0" fit="contain" class="avatar"></el-image>
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
            <el-upload class="avatar-uploader" action="http-request" :http-request="onDoorHeadUrl1" :show-file-list="false">
              <el-image v-if="doorHeadUrl1" :src="doorHeadUrl1" fit="contain" class="avatar"></el-image>
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
            <el-upload class="avatar-uploader" action="http-request" :http-request="onDoorHeadUrl2" :show-file-list="false">
              <el-image v-if="doorHeadUrl2" :src="doorHeadUrl2" fit="contain" class="avatar"></el-image>
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </div>
        </el-form-item>
        <el-form-item label="店铺简介" prop="describes">
          <el-input v-model="form.describes" type="textarea" maxlength="100" show-word-limit></el-input>
        </el-form-item>
        <el-divider></el-divider>
        <el-form-item>
          <el-button class="inquire" type="success" style="width: 120px" round @click="submitForm('form')">提交</el-button>
          <el-button type="danger" style="width: 120px" round>取消</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import areaSelectCmp from '@/components/area_select_cmp/index'

export default {
  name: 'ShopAuthenticate',

  components: {
    areaSelectCmp
  },

  data () {
    return {
      form: {
        shopType: 0, // 店铺类型 0-店铺 2-小超
        shopName: '', // 商家名称
        name: '', // 联系人
        mobile: '', // 联系电话
        area: '', // 地区
        adderss: '', // 详细地址
        tax: '', // 税号
        identityCardUrl: '', // 身份证图片
        orgUrl: '', // 营业执照
        doorHeadUrl: '', // 门头照
        describes: '', // 店铺简介
      },
      identityCardUrlPositive: '', // 身份证正面
      identityCardUrlNegative: '', // 身份证背面
      doorHeadUrl0: '', // 门头照
      doorHeadUrl1: '', // 门头照
      doorHeadUrl2: '', // 门头照
      rules: {
        shopName: [
          { required: true, message: '请输入商铺名称', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入联系人', trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入联系人电话', trigger: 'blur' }
        ],
        area: [
          { required: true, message: '请选择地区', trigger: 'blur' }
        ],
        adderss: [
          { required: true, message: '请输入详细地址', trigger: 'blur' }
        ],
        tax: [
          { required: true, message: '请输入税号', trigger: 'blur' }
        ],
        describes: [
          { required: true, message: '请输入税号', trigger: 'blur' }
        ]
      }
    }
  },

  methods: {
    // 从子组件获取的地区的值
    onChild (e) {
      let area = e[0] + ',' + e[1]
      if (e.length === 3) {
        area = area + ',' + e[2]
      }
      this.form.area = area
    },
    // 身份证正面
    onIdentityCardUrlPositive (params) {
      this._uploadSectionFile(params)
        .then((res) => {
          this.identityCardUrlPositive = res
        })
    },
    // 身份证背面
    onIdentityCardUrlNegative (params) {
      this._uploadSectionFile(params)
        .then((res) => {
          this.identityCardUrlNegative = res
        })
    },
    // 营业执照图片
    onBusinessLicenseUrl (params) {
      this._uploadSectionFile(params)
        .then((res) => {
          this.form.orgUrl = res
        })
    },
    // 门头照
    onDoorHeadUrl0 (params) {
      this._uploadSectionFile(params)
        .then((res) => {
          this.doorHeadUrl0 = res
        })
    },
    // 门头照1
    onDoorHeadUrl1 (params) {
      this._uploadSectionFile(params)
        .then((res) => {
          this.doorHeadUrl1 = res
        })
    },
    // 门头照2
    onDoorHeadUrl2 (params) {
      this._uploadSectionFile(params)
        .then((res) => {
          this.doorHeadUrl2 = res
        })
    },
    // 点击提交表单
    submitForm (formName) {
      if (this._formRules(formName)) {
        if (this.$route.query.id) {
          // console.log(this.form.area)
          // this.form.area = this.form.area.join(',')
          this.form.approvalStatus = 0 // 审批状态 0-待审核 1-已审核
          this.apiUpdateShop()
        } else {
          this._addShop()
        }
      }
    },

    // 私有方法-----------------------------------------------
    // 表单验证
    _formRules (formName) {
      let rulesRes = false
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (!this.identityCardUrlPositive) {
            this.$message.error('请选择身份证正面图片')
            rulesRes = false
            return
          }
          if (!this.identityCardUrlNegative) {
            this.$message.error('请选择身份证背面图片')
            rulesRes = false
            return
          }
          if (!this.form.orgUrl) {
            this.$message.error('请选择营业执照图片')
            rulesRes = false
            return
          }
          this.form.doorHeadUrl = this.doorHeadUrl0 + ',' + this.doorHeadUrl1 + ',' + this.doorHeadUrl2
          this.form.identityCardUrl = this.identityCardUrlPositive + ',' + this.identityCardUrlNegative
          if (!this.form.doorHeadUrl.replace(/,/g, '')) {
            this.$message.error('请选择门头照')
            rulesRes = false
            return
          }
          rulesRes = true
          return
        } else {
          console.log('error submit!!')
          rulesRes = false
          return false
        }
      })
      return rulesRes
    },

    // api接口
    // 上传图片
    _uploadSectionFile (params) {
      const isJPG = params.file.type === 'image/jpeg' || params.file.type === 'image/png'
      const isLt2M = params.file.size / 1024 / 1024 < 10
      if (!isJPG) {
        this.$message.error('上传图片只能是 JPG/PNG 格式!')
        return
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 10MB!')
        return
      }
      const formData = new FormData()
      formData.append('file', params.file)
      return this.$http.post('/common/oss/uploadFile', formData)
        .then((res) => {
          return res.data.data.webUrl
        })
    },
    // 提交认证店铺
    _addShop () {
      this.$http.post('/woho/shop/manage/insert', this.form)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('提交成功')
            this.$router.push({ path: '/shopAuthenticateStatus', params: { submitSuccess: true }})
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 提交重新认证店铺
    apiUpdateShop () {
      this.$http.put('/woho/shop/manage/update', this.form)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('重新提交成功')
            this.$router.push({ path: '/shopAuthenticateStatus', params: { submitSuccess: true }})
          }
        })
    },
    // 回显
    apiGetShopData () {
      this.$http.post('/woho/shop/manage/findByUserId')
        .then((res) => {
          if (res.data.success) {
            this.form = res.data.data
            this.form.area = res.data.data.area.split(',')
            this.form.identityCardUrl = res.data.data.identityCardUrl.split(',')
            this.identityCardUrlPositive = this.form.identityCardUrl[0]
            this.identityCardUrlNegative = this.form.identityCardUrl[1]
            this.form.doorHeadUrl = res.data.data.doorHeadUrl.split(',')
            this.doorHeadUrl0 = this.form.doorHeadUrl[0]
            this.doorHeadUrl1 = this.form.doorHeadUrl[1]
            this.doorHeadUrl2 = this.form.doorHeadUrl[2]
          } else {
            this.message.error(res.data.msg)
          }
        })
    },
  },

  created () {
    // 判断是否是编辑还是新增
    if (this.$route.query.id) {
      // 编辑
      this.apiGetShopData()
      return
    }
  }
}
</script>

<style lang="scss" scoped>
  .box-card {
    min-width: 1100px;
    margin-top:15px;
    .identityCardUrl{
      display: flex;
      .identityCardUrl_box{
        width: 178px;
      }
    }
    .avatar-uploader .el-upload .el-icon-plus{
      border: 1px dashed #d9d9d9 !important;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      // overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #409EFF;
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 178px;
      height: 178px;
      line-height: 178px;
      text-align: center;
    }
    .avatar {
      width: 178px;
      height: 178px;
      display: block;
    }
  }
</style>
