<template>
  <div class="box">
    <LayoutBox one="店铺认证">
      <div v-if="submitSuccess" class="authenticate">
        <el-image class="authenticate_img" :src="require('@/assets/img/authenticate/pass.png')" fit="cover"></el-image>
        <p class="authenticate_text">提交成功，审核中请耐心等待</p>
      </div>
      <div v-else-if="form.approvalStatus == 0" class="authenticate">
        <el-image class="authenticate_img2" :src="require('@/assets/img/authenticate/pass.png')" fit="cover"></el-image>
        <p class="authenticate_remark2">审核中，我们会在3到5个工作日内将审核结果发送至你的手机和APP，请耐心等待。</p>
      </div>
      <div v-else-if="form.approvalStatus == 2" class="authenticate">
        <el-image class="authenticate_img" :src="require('@/assets/img/authenticate/unPass.png')" fit="cover"></el-image>
        <p class="authenticate_text">认证失败</p>
        <p class="authenticate_remark">认证失败，信息核对有误，请重新提交资料，若有疑问，请联系客服。</p>
        <el-button class="inquire" type="success" style="width: 150px" round @click="onAuth">重新认证</el-button>
      </div>

      <div v-else-if="form.approvalStatus == 1">
        <div class="pass_title">
          <el-image class="pass_img" :src="require('@/assets/img/authenticate/pass.png')" fit="cover"></el-image>
          <p>你的店铺已认证</p>
        </div>
        <el-form :model="form" label-width="100px" size="mini">
          <el-form-item label="商家详情："><span>{{ form.shopName }}</span></el-form-item>
          <el-form-item label="联系人："><span>{{ form.name }}</span></el-form-item>
          <el-form-item label="联系电话人："><span>{{ form.mobile }}</span></el-form-item>
          <el-form-item label="地区："><span>{{ form.area }}</span></el-form-item>
          <el-form-item label="详细地址："><span>{{ form.adderss }}</span></el-form-item>
          <el-form-item label="税号："><span>{{ form.tax }}</span></el-form-item>
          <el-form-item label="店铺简介："><span>{{ form.describes }}</span></el-form-item>
          <el-form-item label="身份证：">
            <el-image v-for="(item, i) in form.identityCardUrl" :key="i" class="identityCardUrl" :src="item" fit="contain"></el-image>
          </el-form-item>
          <el-form-item label="营业执照：">
            <el-image class="businessLicenseUrl" :src="form.orgUrl" fit="contain"></el-image>
          </el-form-item>
          <!-- <el-form-item label="卫生许可证：">
            <el-image class="sanitaryOperationLicenseUrl" :src="form.sanitaryOperationLicenseUrl" fit="contain"></el-image>
          </el-form-item> -->
          <el-form-item label="门头照：">
            <div class="doorHeadUrl_group">
              <el-image v-for="(item, i) in form.doorHeadUrl" :key="i" class="doorHeadUrl" :src="item" fit="contain"></el-image>
            </div>
          </el-form-item>
        </el-form>
      </div>

      <div v-else>
        <p class="authenticate_remark">你的店铺尚未认证！</p>
        <router-link to="/shopAuthenticate"><el-button class="inquire" type="success" style="width: 150px" round>立刻去认证</el-button></router-link>
      </div>
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'

export default {
  name: 'ShopAuthenticateStatus',

  components: {
    LayoutBox
  },

  data () {
    return {
      form: {
        shopName: '',
        name: '',
        mobile: '',
        area: '',
        adderss: '',
        tax: '',
        identityCardUrl: [],
        businessLicenseUrl: '',
        // sanitaryOperationLicenseUrl: '',
        doorHeadUrl: [],
        approvalStatus: '' // 审核状态
      },
      submitSuccess: false // 是否显示提交成功页面
    }
  },

  created () {
    this.submitSuccess = this.$route.query.submitSuccess
    this._getShopData()
  },

  mounted () {
  },

  methods: {
    // 点击重新认证
    onAuth() {
      this.$router.push({ path: '/shopAuthenticate', query: { id: this.form.id }})
    },

    // api 接口
    // 获取商铺信息
    _getShopData () {
      this.$http.post('/woho/shop/manage/findByUserId')
        .then((res) => {
          this.form = res.data.data
          if (res.data.data) {
            console.log('有店铺信息')
            this.form.identityCardUrl = res.data.data.identityCardUrl.split(',')
            this.form.doorHeadUrl = res.data.data.doorHeadUrl.split(',')
          } else {
            console.log('没有店铺信息')
          }
        })
    }
  }
}
</script>

<style lang="scss" scoped>
  .box{
    min-width: 1100px
  }
  .authenticate {
    width: 100%;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .authenticate_img{
      width: 149px;
      height: 149px;
    }
    .authenticate_text{
      font-size: 36px;
      color: #666666;
    }
    .authenticate_remark{
      font-size: 24px;
      color: #aaaaaa;
    }
    .authenticate_remark2{
      font-size: 20px;
      color: #333333;
    }
    .authenticate_img2{
      width: 90px;
      height: 90px;
    }
  }
  .pass_title{
    display: flex;
    align-items: center;
    .pass_img{
      width: 28px;
      height: 29px;
      margin-right: 15px;
    }
  }
  .identityCardUrl{
    width: 171px;
    height: 108px;
  }
  .businessLicenseUrl{
    width: 163px;
    height: 231px;
  }
  .sanitaryOperationLicenseUrl{
    width: 246px;
    height: 172px;
  }
  .doorHeadUrl_group{
    display: flex;
    .doorHeadUrl{
      width: 328px;
      height: 185px;
    }
  }

</style>
