import store from '@/store/index'

let mimc_appId = '2882303761518408757'
let mimc_appSecret = '8lcjzwB6JDw0hzKN7zgJUA=='
let mimc_appKey = '5911840874757'
let mimc_appAccount = ''

const MIMC_URL = 'https://mimc.chat.xiaomi.net/api'
let _this

/*eslint-disable*/
class Mimc {
  constructor () {
    _this = this
  }
  /* @note: fetchToken()访问APP应用方自行实现的AppProxyService服务，该服务实现以下功能：
   存储appId/appKey/appSec（不应当存储在APP客户端/html/js）
   用户在APP系统内的合法鉴权
   调用小米TokenService服务，并将小米TokenService服务返回结果通过fetchToken()原样返回 **/
  async fetchGetMIMCToken (loginName) {
    var sendData = {appId: mimc_appId,appKey: mimc_appKey,appSecret: mimc_appSecret,appAccount: loginName}
    let token = await _this.httpRequest(`${MIMC_URL}/account/token`, 'POST', sendData)
    localStorage.setItem('mimcToken', token.data.token)
    return token
  }

  httpRequest(url, method, data) {
    return new Promise ((reslove,reject) => {
      var xhr = new XMLHttpRequest()
      xhr.open(method || 'GET', url, false)
      xhr.setRequestHeader('content-type', 'application/json')
      xhr.setRequestHeader('token', localStorage.mimcToken)
      xhr.setRequestHeader('appId', mimc_appId)
      xhr.setRequestHeader('appKey', mimc_appKey)
      xhr.setRequestHeader('appSecret', mimc_appSecret)
      xhr.send(JSON.stringify(data))
      reslove(JSON.parse(xhr.response))
      reject(JSON.parse(xhr.response))
    })
  }

  fetchMIMCToken () {
    var sendData = {appId:mimc_appId,appKey:mimc_appKey,appSecret:mimc_appSecret,appAccount:mimc_appAccount};
    return _this.httpFetchMIMCToken('https://mimc.chat.xiaomi.net/api/account/token', sendData);
  }

  httpFetchMIMCToken(url, data) {
    var xhr = new XMLHttpRequest();
    xhr.open('POST', url, false);
    xhr.setRequestHeader('content-type', 'application/json');
    xhr.send(JSON.stringify(data));
    return JSON.parse(xhr.response);
  }

  login (loginName) {
    mimc_appAccount = loginName
    _this.user = new MIMCUser(mimc_appId, mimc_appAccount)
    _this.user.registerP2PMsgHandler(_this.receiveP2PMsg)
    _this.user.registerFetchToken(_this.fetchMIMCToken)
    _this.user.registerStatusChange(_this.statusChange)
    _this.user.registerServerAckHandler(_this.serverAck)
    _this.user.registerDisconnHandler(_this.disconnect)
    _this.user.login()
  }

  statusChange(bindResult, errType, errReason, errDesc) {
    if (bindResult) {
      _this.writeToScreens("login succeed")
    } else {
      _this.writeToScreens("login failed.errReason=" + errReason + ",errDesc=" + errDesc + ",errType=" + errType)
    }
  }

  // 监听接收消息
  receiveP2PMsg(message) {
    setTimeout(() => {
      _this._setTimeoutReceiveP2PMsg(message)
    }, 500)
    console.log('receiveP2PMsg(message)监听收到消息', message)
    // var date = new Date(parseInt(message.getTimeStamp()))
    // _this.writeToScreens("time: " + date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate() + " " + date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds() + " " + date.getMilliseconds())
    // _this.writeToScreens(message.getFromAccount() + " to " + mimc_appAccount + ":" + Base64.decode(JSON.parse(message.getPayload()).payload))
  }

  // 延迟加载消息监听为了正确获取修改的extra字段
  _setTimeoutReceiveP2PMsg (message) {
    if (message.getFromAccount() === store.state.mimc.currentUser.id) {
      // 如果发来的消息是当前正在聊天的人就不显示新消息提示
      let data = {
        toAccount: localStorage.userId,
        mimc_appAccount: message.getFromAccount(),
        sequence: message.getSequence(),
        extraValue: 1,
      }
      _this.updateExtra(data)
      let msgJson = {
        fromAccount: message.getFromAccount(),
        message: {
          bizType: JSON.parse(message.getPayload()).bizType || message.getBizType(),
          payload: Base64.decode(JSON.parse(message.getPayload()).payload)
        }
      }
      store.commit('addMessage', msgJson)
    } else {
      store.dispatch('getConversationList')
    }
  }

  // 发送消息
  sendMsg({ userName, sendMessage, bizType }) {
    var toUser = userName
    var message = sendMessage
    var message_ = Base64.encode(message)
    let jsonMsg = String(JSON.stringify({
                        version: 0, 
                        msgId: "TEXT_1234",
                        timestamp: String((new Date()).valueOf()),
                        bizType: bizType,
                        payload: message_}))
    console.log(jsonMsg)
    try {
      var packetId = _this.user.sendMessage(toUser, jsonMsg, bizType)
    } catch (err) {
      console.log("sendMessage fail, err=" + err)
    }
    _this.writeToScreens(mimc_appAccount + " to " + toUser + ":" + message)
  }

    serverAck(packetId, sequence, timeStamp, errMsg) {
      console.log('发送消息后，服务器接收到消息ack的回调')
      let data = {
        toAccount: store.state.mimc.currentUser.id,
        mimc_appAccount: localStorage.userId,
        sequence: sequence,
        extraValue: '0'
      }
      _this.updateExtra(data)
      _this.writeToScreens("receive msg ack:" + packetId + ",sequence=" + sequence + ",ts=" + timeStamp + ",errMsg=" + errMsg)
    }

  // 拉取历史消息
  async messageHistory (userId, count, extra) {
    // count 表示查询的消息条数
    // utcToTime 当前时间，单位毫秒
    // bizType 可用于表示消息类型扩展字段（可选）
    // extra 可用于表示消息全局扩展字段值（可选）
    let toAccount1 = localStorage.getItem('userId') // 消息接收方在APP帐号系统内唯一ID
    let utcToTime = new Date().getUTCDate()
    // let data = {toAccount:toAccount,fromAccount:fromAccount,utcToTime:utcToTime,count:count}
    let data = { toAccount: toAccount1, fromAccount: userId, utcToTime: utcToTime, count: count, extra: extra }
    let res = await _this.httpRequest(`${ MIMC_URL }/msg/p2p/queryOnCount/`, 'POST', data)
    // console.log('历史消息', res)
    // console.log('内容1：', _this._baseMessages(res))
    // let index = _this._baseMessages(res).length - 1
    // console.log('最新消息的上一条消息', _this._baseMessages(res)[index - 1])
    // console.log('最新消息', _this._baseMessages(res)[index])
    // console.log('bizType', _this._baseMessages(res)[index].bizType)
    // console.log('extra', _this._baseMessages(res)[index].extra)
    // console.log('内容', Base64.decode(_this._baseMessages(res)[index].payload))
    return res
  }

    // 解析2层Base64消息的第一层
    _baseMessages (res) {
        return res.data.messages.map((item) => JSON.parse(Base64.decode(item.payload)))
    }

  // 修改历史消息里面的未读已读字段
  async updateExtra ({toAccount, mimc_appAccount, sequence, extraValue}) {
    // toAccount消息接收方在APP帐号系统内唯一ID
    let data = { fromAccount: mimc_appAccount, toAccount: toAccount, sequence: sequence, extraValue: extraValue }
    let res = await _this.httpRequest(`${ MIMC_URL }/msg/p2p/extra/update/v2`, 'POST', data)
    console.log('修改extraValue', res)
  }

  // 获取最近聊天会话
  async getConversationList () {
    let msgExtraFlag = true
    let ts = new Date().getTime() - 1000*60*60*24*30 // 一个月前
    let res = await _this.httpRequest(`${ MIMC_URL }/contact/v2?msgExtraFlag=${msgExtraFlag}&ts=${ts}`)
    return res
  }

    disconnect() {
        console.log('mimc连接断开回调')
        _this.writeToScreens("disconnect")
    }

    logout() {
        console.log('mimc退出登录')
        _this.user.logout()
    }

    writeToScreens(message) {
    }
}

export default Mimc
