<template>
  <div class="box-card">
    <LayoutBox one="店铺信息">
      <h4>店铺信息</h4>
      <el-form ref="shopDataForm" class="form_group" :model="shopData" :rules="rules" label-width="130px">
        <el-form-item label="联系方式" prop="mobile">
          <el-input v-model.number="shopData.mobile" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="经营地址" prop="area">
          <areaSelectCmp :area="shopData.area" @childFn="onChild"></areaSelectCmp>
        </el-form-item>
        <el-form-item prop="adderss">
          <el-input v-model="shopData.adderss" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="店铺介绍" prop="describes">
          <el-input v-model="shopData.describes" type="textarea" :autosize="{ minRows: 6, maxRows: 6}" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="店铺发货时间规则" prop="deliveryRule">
          <el-input v-model="shopData.deliveryRule" type="textarea" :autosize="{ minRows: 6, maxRows: 6}" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item>
          <p class="remark">
            我声明，此页面所填写内容均真实有效，特别是经营地
            址为店铺最新可联系到的地址，同时可以作为行政机关
            和司法机关送达法律文件的地址。如果上述地址信息有
            误，愿意承担由此带来的平台处罚（处罚细则）、行政
            监管和司法诉讼风险。
          </p>
        </el-form-item>
        <el-form-item>
          <el-button class="inquire" type="success" style="width: 120px" round @click="submitForm('shopDataForm')">保存</el-button>
        </el-form-item>
      </el-form>
    </LayoutBox>
  </div>
</template>

<script>
import areaSelectCmp from '@/components/area_select_cmp/index'
import LayoutBox from '@/components/LayoutBox/index'

export default {
  name: 'ShopInfoEdit',

  components: {
    areaSelectCmp,
    LayoutBox
  },

  data () {
    return {
      shopData: {
        mobile: '',
        area: '',
        adderss: '',
        describes: '', // 店铺详情
        deliveryRule: ''
      },
      rules: {
        mobile: [
          { required: true, message: '请输入联系方式', trigger: 'blur' }
        ],
        area: [
          { required: true, message: '请选择经营地址', trigger: 'blur' }
        ],
        adderss: [
          { required: true, message: '请输入详细地址', trigger: 'blur' }
        ]
      }
    }
  },

  created () {
    this._getShopData()
      .then((res) => {
        this.shopData = { ...res }
        this.shopData.area = res.area.split(',')
      })
  },

  mounted () {
  },

  methods: {
    // 从子组件获取的地区的值
    onChild (e) {
      let area = e[0] + ',' + e[1]
      if (e.length === 3) {
        area = area + ',' + e[2]
      }
      this.shopData.area = area
    },
    // 点击保存提交编辑表单
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this._updateShop()
            .then((res) => {
              if (res) {
                this.$message.success('保存成功')
              }
            })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // api接口
    // 获取数据
    _getShopData () {
      return this.$http.post('/woho/shop/manage/findByUserId')
        .then((res) => {
          return res.data.data
        })
    },
    // 修改店铺信息
    _updateShop () {
      // console.log(this.shopData.area.constructor == Array)
      // eslint-disable-next-line
      if (this.shopData.area.constructor == Array) {
        this.shopData.area = this.shopData.area.join(',')
      }
      return this.$http.post('/woho/shop/manage/update', this.shopData)
        .then((res) => {
          if (!res.data.success) this.$message.error(res.data.msg)
          return res.data.success
        })
    }
  }
}
</script>

<style lang="scss" scoped>
  .box-card {
    min-width: 1100px;
    .form_group{
      width: 430px;
      .remark{
        line-height: 20px;
      }
    }
  }
</style>
