<template>
  <div>
    <el-cascader v-if="blArea" v-model="positionRes" :props="props" :size="size" @change="handleChangePosition" />
  </div>
</template>

<script>
import positionJson from '@/assets/json/china_city_data.json'

export default {
  name: 'AreaSelect',

  props: {
    area: {
      type: [String, Array],
      default: ''
    },
    size: {
      type: String,
      default: ''
    }
  },

  data() {
    let _this = this
    return {
      blArea: true,
      // positionRes: this.area,
      positionRes: [],
      // positionRes: ['330000', '330500', '330503'],
      // positionRes: ['浙江省', '湖州市', '南浔区'],
      props: {
        lazy: true,
        async lazyLoad(node, resolve) {
          /*eslint-disable*/
          let position = await _this.positionJsoneFun()
          let nodes = ''
          const { level, value } = node
          switch (level) {
            case (0): {
              nodes = Array.from(position)
                .map(item => ({
                  value: item.id,
                  label: item.name,
                  leaf: false
                  // 通过调用resolve将子节点数据返回，通知组件数据加载完成
                }))
              break
            }
            case (1): {
              let city = []
              city = position.filter((item) => {
                return item.id === value
              })
              nodes = Array.from(city[0].cityList)
                .map(item => ({
                  value: item.id,
                  label: item.name,
                  leaf: false
                }))
              break
            }
            case (2): {
              let city = []
              let district = []
              city = position.filter((item) => {
                return item.id.slice(0, 2) === value.slice(0, 2)
              })
              district = city[0].cityList.filter((item) => {
                return item.id === value
              })
              nodes = Array.from(district[0].cityList)
                .map(item => ({
                  value: item.id,
                  label: item.name,
                  leaf: true
                }))
              break
            }
          }
          resolve(nodes)
        }
      },
    }
  },

  methods: {
    handleChangePosition (e) {
      let position = positionJson
      let i = 0
      let positionRes = []
      e.map(item => {
        switch (i) {
          case (0): {
            position.map((itemm) => {
              if (itemm.id === item) {
                positionRes.push(itemm.name)
              }
            })
            break
          }
          case (1): {
            position.map((itemm) => {
              if (positionRes[0] === itemm.name) {
                itemm.cityList.map((itemmm) => {
                  if (itemmm.id === item) {
                    positionRes.push(itemmm.name)
                  }
                })
              }
            })
            break
          }
          case (2): {
            position.map((itemm) => {
              if (positionRes[0] === itemm.name) {
                itemm.cityList.map((itemmm) => {
                  if (itemmm.name === positionRes[1]) {
                    itemmm.cityList.map((itemmmm) => {
                      if (itemmmm.id === item) {
                        positionRes.push(itemmmm.name)
                      }
                    })
                  }
                })
              }
            })
            break
          }
        }
        i++
      })
      this.$emit('childFn', positionRes)
    },

    positionJsoneFun () {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          if (positionJson) {
            resolve(positionJson)
          }else{
            reject('positionJsone为空')
          }
        }, 1000)
      })
    }
  },

  computed: {
    areaComputed: {
      get () {
        let i = 0
        let area = this.area
        console.log('area1111', area)
        let positionRes = []
        let position = positionJson
        area.map((item) => {
          switch (i) {
            case (0): {
              position.map((itemm) => {
                if (itemm.name === item) {
                  positionRes.push(itemm.id)
                }
              })
              break
            }
            case (1): {
              position.map((itemm) => {
                if (positionRes[0] === itemm.id) {
                  itemm.cityList.map((itemmm) => {
                    if (itemmm.name === item) {
                      positionRes.push(itemmm.id)
                    }
                  })
                }
              })
              break
            }
            case (2): {
              position.map((itemm) => {
                if (positionRes[0] === itemm.id) {
                  itemm.cityList.map((itemmm) => {
                    if (itemmm.id === positionRes[1]) {
                      itemmm.cityList.map((itemmmm) => {
                        if (itemmmm.name === item) {
                          positionRes.push(itemmmm.id)
                        }
                      })
                    }
                  })
                }
              })
              break
            }
          }
          i++
        })
        return positionRes
      }
    }
  },

  watch: {
    area (newName, oldName) {
      if (newName instanceof Array) {
        this.blArea = false
        this.positionRes = this.areaComputed
        // this.positionRes = ['110000', '110001', '110102']
        console.log('this.positionRes', this.positionRes)
      }
      setTimeout(() => {
        this.blArea = true
      }, 1000)
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
