import Vue from 'vue'
import App from './App.vue'
import router from './router'
import HTTP from './http'
import Config from './config/config'
import options from './utils/loading.js'
import api from './utils/api.js'
import VueAMap from 'vue-amap'
import VueJsonp from 'vue-jsonp'

import './plugins/element.js'
import './assets/css/style.scss'
import './assets/css/iconfont.css'
import store from './store'
import TreeTable from 'vue-table-with-tree-grid'
import Mimc from './assets/js/mimc/mimc.js'
import './vendor/Blob'
import './vendor/Export2Excel.js'

Vue.prototype.$http = HTTP.http
Vue.prototype.$logisticsHttp = HTTP.logisticsHttp
Vue.prototype.$Http24 = HTTP.Http24
Vue.prototype.$fileHttp = HTTP.fileHttp
Vue.prototype.$optionsCmp = options
// console.log('Config.websocket', Config.websocket)
Vue.prototype.$websocket = Config.websocket

import LayoutBox from './components/LayoutBox/index.vue'
Vue.config.productionTip = false
Vue.component('layout-box', LayoutBox)
Vue.component('tree-table', TreeTable)

Vue.prototype.$api = api
Vue.prototype.$MIMC = new Mimc()

Vue.directive('has', {
  bind: function (el, binding) {
    if (!Vue.prototype.$_has(binding.value)) {
      // el.parentNode.removeChild(el);
      el.style.display = 'none'
    }
  }
})
// 权限检查
Vue.prototype.$_has = function (value) {
  let isExist = false
  //  获取拥有的权限
  let buttonperms = localStorage.getItem('btnPermissions')
  console.log(1111111, buttonperms) // aa
  // 权限列表
  console.log(value) // aa bb
  // eslint-disable-next-line eqeqeq
  if (buttonperms == value) {
    isExist = true
  }
  return isExist
}
new Vue({
  router,
  store,
  render: h => h(App),
  created() {
    localStorage.setItem('btnPermissions', 'bb')
  }
}).$mount('#app')

Vue.use(VueAMap)
VueAMap.initAMapApiLoader({
  key: '34104d3b9cae724dcbe4250f4b62b361',
  plugin: ['AMap.Scale', 'AMap.OverView', 'AMap.ToolfBar', 'AMap.MapType'],
  v: '1.4.4'
})

Vue.use(VueJsonp)
