<template>
  <div v-if="isRouterAlive" id="app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',

  data() {
    return {
      isRouterAlive: true
    }
  },

  mounted () {
  },

  methods: {
    reload () {
      this.isRouterAlive = false
      this.$nextTick(() => {
        this.isRouterAlive = true
      })
    }
  },

  provide () {
    return {
      reload: this.reload
    }
  },
}
</script>

<style>
  .app{
    overflow-x: auto;
  }
</style>
